export default {
  name: 'editDiv',
  props: {
    value: {
      type: String,
      default: ''
    },
    canEdit: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      innerText: this.value,
      isLocked: false
    }
  },
  watch: {
    'value' (newValue, oldValue) {
      this.innerText = newValue
      this.$nextTick(() => {
        this.setCaretPosition()
      })
    }
  },
  methods: {
    changeText () {
      this.$emit('input', this.$el.innerHTML)
    },
    setCaretPosition () {  // è®¾ç½®å…‰æ ‡åˆ°æœ«å°¾
      const obj = this.$refs.editDiv
      let range
      if (window.getSelection) {
        obj.focus()
        range = window.getSelection()
        range.selectAllChildren(obj)
        range.collapseToEnd()
      } else if (document.selection) {
        range = document.selection.createRange()
        range.moveToElementText(obj)
        range.collapse(false)
        range.select()
      }
    },
    getCursortPosition () {
      const element = this.$refs.editDiv
      var caretOffset = 0
      var doc = element.ownerDocument || element.document
      var win = doc.defaultView || doc.parentWindow
      var sel
      if (win.getSelection != null) { // è°·æ­Œã€ç«ç‹
        sel = win.getSelection()
        if (sel.rangeCount > 0) { // é€‰ä¸­çš„åŒºåŸŸ
          var range = win.getSelection().getRangeAt(0)
          var preCaretRange = range.cloneRange() // å…‹éš†ä¸€ä¸ªé€‰ä¸­åŒºåŸŸ
          preCaretRange.selectNodeContents(element) // è®¾ç½®é€‰ä¸­åŒºåŸŸçš„èŠ‚ç‚¹å†…å®¹ä¸ºå½“å‰èŠ‚ç‚¹
          preCaretRange.setEnd(range.endContainer, range.endOffset)  // é‡ç½®é€‰ä¸­åŒºåŸŸçš„ç»“æŸä½ç½®
          caretOffset = preCaretRange.toString().length
        }
      } else if ((sel = doc.selection) && sel.type !== 'Control') { // IE
        var textRange = sel.createRange()
        var preCaretTextRange = doc.body.createTextRange()
        preCaretTextRange.moveToElementText(element)
        preCaretTextRange.setEndPoint('EndToEnd', textRange)
        caretOffset = preCaretTextRange.text.length
      }
      return caretOffset
    }
  }
}



